<!-- 需求管理查看详情 -->
<template>
  <div class="releaseDetails">
    <!-- 背景图 -->
    <TopImg :title="topImg.title" :content="topImg.content" :image="topImg.bgImg" :bl="topImg.bl"></TopImg>
    <div class="barPath width">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/release' }">供需市场</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/release' }">需求管理</el-breadcrumb-item>
        <el-breadcrumb-item>需求名称</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="con width">
      <span class="shu"></span>
      <div class="title">{{listData.title}}</div>
      <div class="serveName" v-if="listData.status == 2">完成</div>
      <div class="serveName" v-if="listData.status == 3">服务商招标</div>
      <div class="serveName" v-if="listData.status == 4">预选服务商</div>
      <div class="serveName" v-if="listData.status == 5">线下对接</div>
      <div class="ss">{{listData.corporate}}</div>
    </div>
    <div class="ll width">
      <span class="shu"></span>
      <div class="item">
        <div class="item_ss">
          <span>提交时间：</span>
          <span>{{listData.end_time |formatDate4}}</span>
        </div>
        <div class="item_ss">
          <span>提交时间：</span>
          <span>{{listData.end_time |formatDate4}}</span>
        </div>
        <div class="item_ss">
          <span>提交时间：</span>
          <span>{{listData.end_time |formatDate4}}</span>
        </div>
      </div>
    </div>
    <div class="details width">
      <span class="shu"></span>
      <div class="lls clearfix">
        <span>需求详情：</span>
        <p>{{listData.content}}</p>
      </div>
    </div>
    <!-- 底部 -->
    <div class="el-footer">
      <Footer />
    </div>
  </div>
</template>

<script>
import TopImg from '../components/TopImg'
import Footer from '../components/Footer.vue'
export default {
  name: '',
  components: { TopImg, Footer },
  props: {},
  data() {
    return {
      topImg: {
        title: '需求管理',
        bgImg: require('../assets/images/u2717.jpg'),
        content: '软硬件服务需求发布',
        bl: false,
      },
      listData: {},
      infoPar: '',
    }
  },
  computed: {},
  watch: {},
  created() {
    var zg = Object.values(this.$route.params)
    if (zg.length > 0) {
      sessionStorage.setItem('appinfoParss', JSON.stringify(this.$route.params))
    }
    this.getData()
  },
  mounted() {},
  methods: {
    async getData() {
      this.infoPar = JSON.parse(sessionStorage.getItem('appinfoParss'))
      const res = await this.$http.post('/demandInfo', this.infoPar)
      this.listData = res.data.data
    },
  },
}
</script>
