  position: relative;
  padding: 20px 40px;
  box-sizing: border-box;
  background-color: #fff;
  .shu {
    position: absolute;
    width: 6px;
    height: 46px;
    left: 20px;
    top: 10px;
    background-color: #1677ff;
  }
  .title {
    font-size: 24px;
    font-weight: 700;
  }
  .serveName {
    font-weight: 400;
    color: #f59a23;
