            justify-content: start;
          }
        }

        .wei {
          i {
            color: red;
          }
        }
      }
    }
  }

  .itemLabLast {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      display: block;
      width: 120px;
      height: 120px;
      border-radius: 50%;
    }
  }
}

.box-card {
  margin-top: 10px;
}

.resCenter {
  span {
    float: left;
